.dates {
  height: 50vh;
  display: flex;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

.dates-container {
  height: 50vh;
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #151515;
  h1 {
    font-weight: 800;
    font-size: 2rem;
    margin-top: 1rem;
    color: #fbc403;
    text-shadow: 1px 1px #ca9f01;
  }
}

.subdates-container {
  height: 15%;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  span {
    font-weight: bold;
    font-size: 1.75rem;
  }
}

.month {
  color: #354359;
  text-transform: uppercase;
}

.day {
  color: #c41019;
}

@media only screen and (max-width: 768px) {
  .dates {
    height: 80vh;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .dates-container {
    height: auto;
    width: 100%;
  }
}
